.container {
  padding: 20px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.title {
  font-size: 24px;
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 20px;
  text-align: center;
}

.select {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: #f9fafb;
  margin-bottom: 20px;
  transition: border-color 0.3s ease;
}

.select:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.deleteButton {
  padding: 12px;
  background: #ef4444;
  color: #ffffff;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s ease;
}

.deleteButton:hover {
  background: #dc2626;
}

.loader {
  text-align: center;
  font-size: 18px;
  color: #4b5563;
}

.error {
  padding: 12px;
  border-radius: 6px;
  background: #f8d7da;
  color: #721c24;
  text-align: center;
  font-weight: 500;
}

.notification {
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 500;
}

.notification--success {
  background: #d4edda;
  color: #155724;
}

.notification--error {
  background: #f8d7da;
  color: #721c24;
}